import { FileHelperService } from "./../../services/file-helper.service";
import { BehaviorSubject } from "rxjs";
import { FormControl } from "@angular/forms";
import { FormGroup } from "@angular/forms";
import { SmartAudioService } from "src/app/services/smart-audio.service";
import { IMService } from "src/app/services/im.service";
import { Component, OnInit, ChangeDetectorRef } from "@angular/core";
import { gsap, TimelineMax } from "gsap";
import { UntilDestroy, untilDestroyed } from "@ngneat/until-destroy";
@UntilDestroy()
@Component({
  selector: "app-send-message",
  templateUrl: "./send-message.component.html",
  styleUrls: ["./send-message.component.scss"],
})
export class SendMessageComponent implements OnInit {
  form = new FormGroup({
    sendInput: new FormControl(""),
  });
  timerSubject = new BehaviorSubject<number>(0);
  public recordTimer = 0;
  recordIntervalHandle = null;
  recordingMicAnimationHandle = null;
  isAudioRecording = false;
  disableRecordButton = false;
  constructor(
    public imService: IMService,
    private smartAudio: SmartAudioService,
    public ref: ChangeDetectorRef,
    private fileHelper: FileHelperService
  ) {}
  entered(input) {
    if ((input.value as string).trim() != "") {
      this.imService.sendMessage((input.value as string).trim());
      input.value = "";
    }
  }
  recordSeconds() {
    return new Date(1970, 0, 1).setSeconds(this.recordTimer);
  }
  stopRecording() {
    //this.smartAudio.play("beep1");

    this.isAudioRecording = false;
    const recordAnim = new TimelineMax();
    recordAnim.to("#micCol", { duration: 0.2, width: "auto" });
    recordAnim.to(".hide-on-record", { duration: 0.1, width: "auto" }, 0);
    recordAnim.to(".record-timer", { duration: 0.2, scale: 0, width: 0 }, 0);
    this.recordingMicAnimationHandle.pause();
    clearInterval(this.recordIntervalHandle);
    this.smartAudio.stopRecord();
  }
  startRecording() {
    //this.smartAudio.waveForm("beep1");
    this.smartAudio.checkPermission().then((result) => {
      if (result) {
        this.record();
      } else {
        this.disableRecordButton = false;
        this.smartAudio.askPermission();
      }
    });
  }
  record() {
    const recordAnim = new TimelineMax();
    recordAnim.to("#micCol", { duration: 0.2, width: "100%" });
    recordAnim.to(".hide-on-record", { duration: 0.1, width: 0 }, 0);
    recordAnim.to(
      ".record-timer",
      { duration: 0.2, scale: 1, width: "auto" },
      0
    );
    this.recordingMicAnimationHandle = new TimelineMax({ repeat: -1 });
    this.recordingMicAnimationHandle
      .to("#recordingMic", { duration: 0.4, opacity: 0 })
      .to("#recordingMic", { duration: 0.4, opacity: 1 });
    this.recordTimer = 0;
    this.timerSubject.next(this.recordSeconds());
    clearInterval(this.recordIntervalHandle);
    this.recordIntervalHandle = setInterval(() => {
      this.recordTimer++;
      this.timerSubject.next(this.recordSeconds());
    }, 1000);

    this.isAudioRecording = true;
    this.smartAudio.startRecord().subscribe((audioBlob) => {
      this.imService.sendAudio(audioBlob);
    });
  }
  toggleRecording() {
    this.disableRecordButton = true;
    this.ref.detectChanges();
    if (this.isAudioRecording) {
      this.stopRecording();
    } else this.startRecording();
  }
  showSendButton() {
    const sendAnim = new TimelineMax();
    sendAnim.to("#micButton", {
      duration: 0.3,
      width: 0,
      scale: 0,
      opacity: 0,
    });
    sendAnim.to(
      "#attachButton",
      { duration: 0.3, scale: 0, width: 0, opacity: 0 },
      0
    );
    sendAnim.to(
      "#sendButton",
      { duration: 0.3, scale: 1, opacity: 1, width: "auto", ease: "back.out" },
      0
    );
  }
  hideSendButton() {
    const sendAnim = new TimelineMax();
    sendAnim.to("#micButton", {
      duration: 0.3,
      scale: 1,
      width: "auto",
      opacity: 1,
      ease: "back.out",
    });
    sendAnim.to(
      "#attachButton",
      { duration: 0.3, scale: 1, width: "auto", opacity: 1 },
      0
    );
    sendAnim.to(
      "#sendButton",
      { duration: 0.3, scale: 0, width: 0, opacity: 0 },
      0
    );
  }

  ngOnInit() {
    this.form.controls.sendInput.valueChanges.subscribe((value) => {
      this.imService.noteTyping();
      if ((value as string).trim() != "") {
        this.showSendButton();
      } else {
        this.hideSendButton();
      }
    });
    this.smartAudio.recordingStarted
      .pipe(untilDestroyed(this))
      .subscribe(() => {
        this.isAudioRecording = true;
        this.disableRecordButton = false;
        this.ref.detectChanges();
      });
    this.smartAudio.recordingStopped
      .pipe(untilDestroyed(this))
      .subscribe(() => {
        this.isAudioRecording = false;
        this.disableRecordButton = false;
        this.ref.detectChanges();
      });
  }
  handleFile(file) {
    this.fileHelper.checkPermission().then((result) => {
      if (result) {
        this.imService.sendImage(file[0]);
        return;
      } else {
        
        this.fileHelper.askPermission();
      }
    });
  }
}
